}

.slick-next {
  padding-right: 5px;
  right: 0;
}

.slick-next:before {
  content: url("images/ic_keyboard_backspace_black_24px.svg");
  -webkit-transform: rotate(-180deg);
          transform: rotate(-180deg);
}

@media only screen and (max-width: 1200px) {
  .-under-body {
    padding-bottom: 70px;
  }
  .main-menu {
    height: 202px;
  }
  .feedback-top {
    width: 500px;
  }
  .feedback-top a {
    font-size: 20px;
  }
  .feedback-top .phone {
    font-size: 26px;
  }
  .menu-top.fixed-top {
    width: 960px;
  }
  .nav.nav-fill li {
    font-size: 22px;
  }
  .nav.nav-fill li a {
    height: 50px;
  }
  .main-topic h1 {
    font-size: 45px;
  }
  .main-topic p {
    font-size: 22px;
  }
  .-img-of-windows {
    padding: 30px 60px 0 40px;
  }
  .container.-gallery {
    padding-bottom: 45px;
  }
  .footer {
    padding-bottom: 0;
  }
  .footer .container {
    padding-top: 20px;
  }
  .footer .contact-in-footer {
    margin-top: 30px;
  }
  .footer .contact-in-footer p {
    margin-bottom: 4px;
  }
  .menu-bottom {
    height: 50px;
  }
}

@media only screen and (max-width: 992px) {
  .main-menu {
    height: 166px;
  }
  .logo-menu-top {
    width: 30%;
    height: 100%;
  }
  .feedback-top {
    width: 350px;
  }
  .feedback-top a {
    font-size: 16px;
  }
  .feedback-top .work-time {
    font-size: 14px;
  }
  .feedback-top .phone {
    font-size: 20px;
  }
  .menu-top.fixed-top {
    width: 720px;
  }
  .nav.nav-fill li {
    font-size: 17px;
  }
  .nav.nav-fill li a {
    height: 40px;
  }
  .nav.nav-fill li a.menu-link-large {
    width: 190px;
  }
  .sidebar-left {
    margin-right: 0 !important;
    max-width: 100% !important;
    margin-bottom: 20px;
  }
  .sidebar-left .sidebar {
    padding-bottom: 20px;
  }
  .sidebar .btn .ion-arrow-up-b {
    margin-left: 10px;
  }
  .-main-content {
    padding: 0;
  }
  .main-topic h1 {
    font-size: 45px;
  }
  .main-topic p {
    font-size: 20px;
  }
  .footer .container {
    height: 180px;
  }
  .menu-bottom {
    height: 40px;
  }
}

@media only screen and (max-width: 768px) {
  .container {
    padding: 15px;
    padding-bottom: 35px;
  }
  .-under-body {
    padding-top: 0;
  }
  .main-menu {
    height: auto;
  }
  .top-contact {
    display: none;
  }
  .navbar-expand-md {
    padding: 10px;
  }
  .navbar-brand {
    display: block;
  }
  .navbar-brand img {
    width: 100px;
  }
  .main-menu {
    margin-bottom: 25px;
    padding: 0px;
  }
  .menu-top.fixed-top {
    width: 510px;
  }
  .navbar-toggler {
    position: relative;
    display: inline-block;
    width: 50px;
    height: 50px;
    padding: 0;
  }
  .navbar-toggler.moveIconToggler .ion-arrow-expand {
    top: -100%;
  }
  .navbar-toggler.moveIconToggler .ion-close-round {
    top: 0%;
  }
  .navbar-toggler .icon-toggler {
    height: 40px;
    width: 40px;
  }
  .navbar-toggler .icons-animation, .navbar-toggler .ion-close-round, .navbar-toggler .ion-arrow-expand {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
  }
  .navbar-toggler .ion-close-round::before, .navbar-toggler .ion-arrow-expand::before {
    display: block;
    font-size: 2em;
  }
  .navbar-toggler .icons-animation {
    overflow: hidden;
  }
  .navbar-toggler .ion-close-round, .navbar-toggler .ion-arrow-expand {
    transition: top .3s;
  }
  .navbar-toggler .ion-arrow-expand {
    top: 0;
  }
  .navbar-toggler .ion-close-round {
    top: 100%;
  }
  .menu-top li {
    border-right: none !important;
    width: 100%;
  }
  .main-topic {
    padding: 0 15px !important;
  }
  .main-topic h1 {
    font-size: 35px;
  }
  .main-topic p {
    font-size: 16px;
  }
  .topic-container {
    font-size: 24px;
  }
  .-img-of-windows {
    padding: auto;
    max-width: 100% !important;
  }
  .img-of-windows-in-incision {
    height: 350px;
  }
  .see-all-reviews {
    float: left !important;
  }
  .-gallery {
    padding-top: 25px;
    width: 100% !important;
  }
  .footer .container {
    padding: 15px;
    height: 100px;
  }
  .menu-bottom {
    display: none;
  }
  .contact-in-footer {
    margin: 15px;
  }
  .contact-in-footer span {
    font-size: 18px;
  }
}

@media only screen and (max-width: 576px) {
  .-under-body {
    padding: 0;
  }
  .container, .sidebar-left {
    border-radius: 0;
  }
  .sidebar-left {
    padding: 0px;
  }
  .topic-container {
    width: 100%;
  }
  .topic-review {
    padding-left: 20px;
  }
  .slider-nav {
    padding: 0 50px;
  }
  .-using .media-body, .-processor .media-body {
    margin-top: 15px;
    margin-left: 0;
  }
}

